<template>
  <div>
    <el-dialog :visible.sync="visible" :before-close="beforeClose" :append-to-body="false" :modal-append-to-body="false"
               title="选择人员" width="800px">
      <div class="role_dialog df pb20">
        <div>
          <div class="mb5" style="font-size: 16px">部门树</div>
          <div class="p15 w300 bl bt br bb of">
            <el-row>
              <el-col :span="20">
                <Tree @callback="treeCallback" :reload="reloadTree" @finishReload="finishReload" :propData=true :propTree="this.treeData" ></Tree>
              </el-col>
            </el-row>
          </div>
        </div>
        <div class="select_dialog">
          <div>
            <div class="mb5" style="font-size: 16px">部门人员</div>
            <div class="p15 w300 bl bt br bb of">
              <el-row>
                <el-col :span="20" v-if="staffList.length" class="ml10 mr10 mt10 mb10">
                  <el-checkbox :disabled="!totalLimit" :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">{{departmentInfo['department_name']}}</el-checkbox>
                  <div class="mt10 mb10"></div>
                  <el-checkbox-group class="ml10" :min='limitLength.min' :max="totalLimit" v-model="checkedEmployees" @change="handleCheckedDepartmentStaffChange">
                    <el-checkbox v-for="staff in staffList" :label="staff" :key="staff['id']">{{staff['staff_name']}}</el-checkbox>
                  </el-checkbox-group>
                </el-col>
              </el-row>
            </div>
          </div>
          <div>
            <div class="mb5" style="font-size: 16px">已选人员</div>
            <div class="p15 w300 bl bt br bb of">
              <el-row>
                <el-col :span="20" class="ml10 mr10 mt10 mb10">
                  <el-checkbox-group :min='limitLength.min' :max="limitLength.max" class="ml10" v-model="checkedEmployees" @change="handleCheckedDepartmentStaffChange">
                    <el-checkbox v-for="staff in checkedEmployees" :label="staff" :key="staff['id']">{{staff['staff_name']}}</el-checkbox>
                  </el-checkbox-group>
                </el-col>
              </el-row>
            </div>
          </div>
        </div>

      </div>
      <div slot="footer" class="dialog-footer mt10" style="text-align: center">
        <el-button type="primary" @click="closeDialog('save')">保 存</el-button>
        <el-button @click="closeDialog('close')">取 消</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  import Tree from './Tree.vue'
  import { mapActions, mapGetters } from 'vuex'

  export default {
    name: 'Dialog',
    components: {Tree},
    props: ['dialogFormVisible', 'limit'],
    computed: {
      visible: {
        get: function () {
          return this.dialogFormVisible
        },
        set: function () {
        }
      },
      treeData() {
        return this.staffTree()
      },
      limitLength() {
        const obj = {min: 0, max: 9999}
        console.log(obj)
        if(this.limit) {
          if(this.limit instanceof Array) {
            obj.min = Number(this.limit[0]) || 0
            obj.max = Number(this.limit[1]) || 9999
          } else {
            obj.max = Number(this.limit) || 9999
          }
        }
        return obj
      }
    },
    data() {
      return {
        totalLimit: 9999,
        checkAll: false,
        showModal: false,
        reloadTree: false,
        treeBackData: {},
        departmentInfo: {},
        staffList: [],
        checkedEmployees: [],
        isIndeterminate: true
      }
    },
    mounted() {
      this.initTree()
    },
    methods: {
      ...mapActions(['getStaffTree']),
      ...mapGetters(['staffTree']),
      closeDialog(method) {
        this.$emit('closeDialog', this.checkedEmployees)
        if (method !== 'close') {
          this.reloadTree = true
        }
      },
      dataHandler(data) {
        this.departmentInfo = {department_id: data.department_id || '', department_name: data.department_name || ''}
        this.staffList = [...data['staff_list'] || []]
      },
      modalShow() {
        this.showModal = true
      },
      closeModal(value) {
        this.form.department = value
      },
      beforeClose(done) {
        done()
        this.$emit('closeDialog')
      },
      finishReload() {
        this.reloadTree = false
      },
      treeCallback(obj) {
        if (obj['_data']) {
          this.dataHandler(obj['_data'])
        }
      },
      initTree() {
        this.getStaffTree()
      },
      handleCheckAllChange(value) {
        if (value) {
          const addArr = []
          this.staffList.forEach(staff => {
            let index = 1
            if(this.checkedEmployees.length) {
              this.checkedEmployees.forEach(check => {
                if (staff.id === check.id) {
                  index = 0
                }
              })
            }
            if (index) {
              addArr.push(staff)
            }
          })
          this.checkedEmployees = [...this.checkedEmployees, ...addArr]
        } else {
          this.staffList.forEach(staff => {
            let position = null
            if(this.checkedEmployees.length) {
              this.checkedEmployees.forEach((check, index) => {
                if (staff.id === check.id) {
                  position = index
                }
              })
            }
            if (position || position === 0) {
              this.checkedEmployees.splice(position, 1)
            }
          })
        }
        this.isIndeterminate = false;
      },
      handleCheckedDepartmentStaffChange(value) {
        this.computedCheck(value)
      },
      computedCheck(value) {
        let data = value || this.checkedEmployees
        let checkedCount = 0
        if (data.length) {
          data.forEach(v => {
            if (this.staffList.length) {
              this.staffList.forEach(staff => {
                if (staff.id === v.id) {
                  checkedCount += 1
                }
              })
            }
          })
        }
        this.checkAll = checkedCount === this.staffList.length;
        this.isIndeterminate = checkedCount > 0 && checkedCount < this.staffList.length;
      }
    },
    watch: {
      staffList: function() {
        if (this.staffList.length === 0) {
          this.checkAll = false
          this.isIndeterminate = true
        } else {
          this.computedCheck()
        }
      },
      checkedEmployees: function() {
        console.log(this.checkedEmployees)
        const length = this.checkedEmployees.length
        console.log(this.limitLength)
        const max = this.limitLength.max
        this.totalLimit = max - length
        console.log(this.totalLimit)
      }
    }
  }
</script>
<style lang="scss" scoped>
  .role_dialog{
    justify-content: space-around;
    height: 360px;
  }
  .of{
    height: 100%;
    overflow: auto;
  }
  .select_dialog{
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    >div{
      height: 150px;
    }
  }
</style>


