<template>
  <div>
    <el-dialog :visible.sync="visible" :modal-append-to-body="false" :before-close="beforeClose" title="编辑部门" width="500px">
      <div class="p15">
        <el-form :show-message=true :inline-message=true ref="form" :model="form" :rules="rules" class="form-text f13 " label-width="108px" size="mini">
          <el-row>
            <el-col :span="20">
              <el-form-item label="部门名称：" prop="department_name">
                <el-input
                  placeholder='请输入部门名称'
                  v-model='form.department_name'>
                </el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row class="mt5">
            <el-col :span="20">
              <el-form-item label="部门编码："  prop="department_code">
                <el-input
                  placeholder='请输入部门编码'
                  v-model='form.department_code'>
                </el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row class="mt5">
            <el-col :span="20">
              <el-form-item label="上级部门：" prop="parent_dept_id">
                <el-input
                  placeholder='请输入上级部门'
                  v-model=form.parent_dept_name>
                </el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row class="mt5">
            <el-col :span="20">
              <el-form-item label="部门负责人：">
                <el-input
                  placeholder='请选择部门负责人'
                  @click.native="showManagerSelect"
                  v-model="dutyManagers"
                  readonly>
                </el-input>
                <!--<el-input-->
                <!--placeholder='请选择部门负责人'-->
                <!--v-model='form.dutyManagerNames'>-->
                <!--</el-input>-->
              </el-form-item>
            </el-col>
          </el-row>
          <el-row style="margin-top: 11px">
            <el-col :span="20">
              <el-form-item label="资产管理人：" prop="asset_manager_name">
                <el-input
                  placeholder='请选择资产管理人'
                  @click.native="showAssetSelect"
                  v-model="form.asset_manager_name"
                  readonly>
                </el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row class="mt5">
            <el-col :span="20">
              <el-form-item label="状态：">
                <el-radio-group v-model="form.status">
                  <el-radio :label="1">启用</el-radio>
                  <el-radio :label="0">禁用</el-radio>
                </el-radio-group>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer mt10" style="text-align: center">
        <el-button type="primary" @click="closeDialog('save')">保 存</el-button>
        <el-button @click="closeDialog('close')">取 消</el-button>
      </div>
    </el-dialog>
    <RoleSelect @closeDialog="closeManagerDialog" :dialogFormVisible="managerDialogShow"></RoleSelect>
    <RoleSelect @closeDialog="closeAssetDialog" :dialogFormVisible="assetDialogShow" :limit="assetLimit"></RoleSelect>
  </div>
</template>
<script>
  import { operateDepartment } from '../constants'
  import RoleSelect from '../common/RoleSelect.vue'

  export default {
    name: 'Dialog',
    props: ['dialogFormVisible', 'formData'],
    components: {RoleSelect},
    computed: {
      visible: {
        get: function () {
          return this.dialogFormVisible
        },
        set: function () {
        }
      },
      rules() {
        return operateDepartment.rules
      }
    },
    data() {
      return {
        assetLimit: 1,
        assetDialogShow: false,
        managerDialogShow: false,
        form: this.cloneObjectKeys(this.formData),
        dutyManagers: ''
      }
    },
    methods: {
      cloneObjectKeys(data) {
        const tmp = {}
        Object.keys(data).map(key => tmp[key] = data[key])
        return tmp
      },
      deleteManager(tag, index) {
        console.log(tag)
        console.log(this.form)
      },
      closeDialog(method) {
        this.$emit('closeDialog', this.form, method)
      },
      showManagerSelect() {
        this.managerDialogShow = true
      },
      showAssetSelect() {
        this.assetDialogShow = true
      },
      closeAssetDialog(value) {
        if (value && value.length) {
          this.form['asset_manager_name'] = value[0].staff_name || ''
          this.form['asset_manager_id'] = value[0].id || ''
        }
        this.assetDialogShow = false
      },
      closeManagerDialog(value) {
        if (value && value.length) {
          const arr = []
          const dutyManagers = []
          value.forEach(v => {
            const obj = {}
            obj['duty_manager_id'] = v.id
            obj['duty_manager_name'] = v.staff_name
            arr.push(obj)
            dutyManagers.push(obj['duty_manager_name'])
          })
          this.form['duty_manager_list'] = arr
          this.dutyManagers = dutyManagers.join('、')
        }
        this.managerDialogShow = false
      },
      beforeClose(done) {
        done()
        this.$emit('closeDialog', {}, 'close')
      }
    },
    watch: {
      formData() {
        this.form = this.cloneObjectKeys(this.formData)
      }
    }
  }
</script>
<style lang="scss">
  .dialog_input_custom{
    >input{
      height: 28px;
      font-size: 12px;
      top: 1px;
      position: relative;
    }
  }
  .dialog_date_custom{
    .el-input__prefix{
      top: -6px;
    }
  }
</style>


