<template>
  <div class='main'>
    <div class='contain df'>
      <div class='contain-aside'>
        <div class='slide-main'>
          <Tree @callback="treeCallback" :reload="reloadTree" @finishReload="finishReload"></Tree>
        </div>
      </div>
      <div class='contain-main mr50' style="text-align: right">
        <div class='action-bar'>
          <el-button type='primary' size='small' @click="dialogShow('import')">导入部门</el-button>
          <el-button type='primary' size='small' @click="dialogShow('edit')">编辑</el-button>
          <el-button type='primary' :disabled="!this.treeBackData.nodeLayer || Number(this.treeBackData.nodeLayer) > 2" size='small' @click="dialogShow('add')">新增子部门</el-button>
          <el-button type='default' :disabled="!this.treeBackData.nodeLayer || Number(this.treeBackData.nodeLayer) === 1" size='small' @click="deleteClick">删除</el-button>
        </div>
        <div class='page-main'>
          <div class="pl50 mt50" v-if="department">
            <el-form class="form-text f13 mt5" label-width='170px' size="mini" style="text-align: left">
              <el-row class="mt10">
                <el-col :span="12">
                  <el-form-item label="部门名称：">{{department['department_name'] || '无'}}</el-form-item>
                </el-col>
              </el-row>
              <el-row class="mt10">
                <el-col :span="22">
                  <el-form-item label="部门编码：">{{department['department_code'] || '无'}}</el-form-item>
                </el-col>
              </el-row>
              <el-row class="mt10">
                <el-col :span="22">
                  <el-form-item label="上级部门：">{{department['parent_dept_name'] || '无'}}</el-form-item>
                </el-col>
              </el-row>
              <el-row class="mt10">
                <el-col :span="22">
                  <el-form-item label="部门负责人：">{{mapManager(department['duty_manager_list']) || '无'}}</el-form-item>
                </el-col>
              </el-row>
              <el-row class="mt10">
                <el-col :span="22">
                  <el-form-item label="资产管理人：">{{department['asset_manager_name'] || '无'}}</el-form-item>
                </el-col>
              </el-row>
              <el-row class="mt10">
                <el-col :span="12">
                  <el-form-item label="状态：">
                    <el-radio-group v-model="department.status">
                      <el-radio :label="1">启用</el-radio>
                      <el-radio :label="0">禁用</el-radio>
                    </el-radio-group>
                  </el-form-item>
                </el-col>
              </el-row>
            </el-form>
          </div>
        </div>
      </div>
    </div>
    <div class="dialog" v-if="department">
      <AddDialog @closeDialog="closeAddDialog" :dialogFormVisible="addDialog"
                 :selectDepartment="this.treeBackData.selectDepartment"></AddDialog>
      <EditDialog @closeDialog="closeEditDialog" :dialogFormVisible="editDialog" :formData="department"></EditDialog>
      <ImportDialog @closeDialog="closeImportDialog" :dialogFormVisible="importDialog"></ImportDialog>
    </div>
  </div>
</template>

<script>
  import AddDialog from './AddDialog.vue'
  import EditDialog from './EditDialog.vue'
  import ImportDialog from './ImportDialog.vue'
  import {mapActions, mapGetters} from 'vuex'
  import {showConfirm} from '@/utils/util'
  import Tree from '../common/Tree.vue'

  export default {
    components: {
      AddDialog, EditDialog, ImportDialog, Tree
    },
    data() {
      return {
        addDialog: false,
        editDialog: false,
        importDialog: false,
        reloadTree: {},
        treeBackData: {}
      }
    },
    mounted() {
    },
    computed: {
      department() {
        if (this.departmentInfo().id) {
          return this.departmentInfo()
        }
        return false
      }
    },
    methods: {
      ...mapActions(['getDepartment', 'postDepartment', 'editDepartment', 'deleteDepartment']),
      ...mapGetters(['departmentInfo']),
      treeCallback(obj) {
        console.log('treeCallback')
        console.log(obj)
        this.treeBackData = obj
        if (this.treeBackData.id) {
          this.getDepartment(this.treeBackData.id)
        }
      },
      finishReload() {
        this.reloadTree = false
      },
      deleteClick() {
        if (this.treeBackData.id) {
          let callback = function () {
            console.log(this)
            this.deleteDepartment(this.treeBackData.id).then(() => {
              this.reloadTree = {reload: true, init: true}
            })
          }
          let msg = '确定删除该部门吗？'
          showConfirm(this, msg, callback)
        }
      },
      mapManager(list) {
        if (list && list.length) {
          return list.forEach(l => l['duty_manager_name']).join('、')
        }
      },
      closeEditDialog(value, method) {
        switch (method) {
          case 'close':
            this.editDialog = false
            break
          case 'save':
            this.editDepartment(value).then(() => {
              this.reloadTree = {reload: true, init: false}
            })
            this.editDialog = false
            break
        }
      },
      closeImportDialog() {
        // TODO 导入API
        this.reloadTree = {reload: true, init: true}
      },
      closeAddDialog(value, method) {
        switch (method) {
          case 'close':
            this.addDialog = false
            break
          case 'save':
            this.postDepartment(value).then(() => {
              this.reloadTree = {reload: true, init: false}
            })
            this.addDialog = false
            break
        }
      },
      dialogShow(type) {
        this[`${type}Dialog`] = true
      },
      handleClick() {
        let params = {name: 'billDtail'}
        this.$router.push(params)
      },
    }
    ,
  }
</script>
<style lang='scss' src='./style.scss'>
</style>


